#responsive-tester {
  background-color: #999;
  bottom: .5rem;
  color: rgba(255, 255, 255, 0);
  display: block;
  line-height: 1;
  padding: .3em .5em;
  position: fixed;
  left: .5rem;
  width: 1rem;
  height: 1rem;
  overflow: hidden;
  border-radius: 3rem;
  z-index: 10000;
}

#responsive-tester:before, #responsive-tester:after {
  display: none;
}

#responsive-tester:hover {
  background-color: #000;
  color: #fff;
  width: auto;
  height: auto;
  border-radius: 0;
}

#responsive-tester:hover:before, #responsive-tester:hover:after {
  display: inline;
}

#responsive-tester:before {
  content: '0px';
}

#responsive-tester:after {
  content: "360px";
}

.accordion > div.open .header {
